.main {
    min-height: 100vh;
    color: darkslategrey;

    &__row {
        display: flex;
        justify-content: space-between;
        min-height: 100vh;

        @media (max-width: 767px) {
            align-items: center;
            flex-direction: column;
            justify-content: center;
            margin: 40px 0 0 0;
        }
    }

    &__col {
        flex: 1 1 50%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 10px;

        img {
            max-width: 350px;
        }

        @media (max-width: 767px) {
            flex: 1 1 100%;
        }
    }

    &__col img {
        width: 100%;
    }

    &__info {
        font-size: 18px;
        text-decoration: underline;
    }

    // &__name {
    //     margin: 0;
    //     padding: 0;
    //     font-size: 70px;
    //     opacity: 0.5;

    //     @media (max-width: 480px) {
    //         font-size: 48px;
    //     }
    // }
}

.image-block {
    align-items: center;

    @media (max-width: 767px) {
        justify-content: flex-start;
    }
    
}