.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0px 10px;
}

.title {
  font-size: 32px;
  font-weight: 600px;
  text-align: center;
}

.title-big {
  margin: 0;
  padding: 0;
  font-size: 70px;
  opacity: 0.5;

  @media (max-width: 480px) {
      font-size: 48px;
  }
}

.square-wrapper {
  border: 2px dashed #373737;
  width: 200px;
  height: 200px;
}
.square {
  width: 200px;
  height: 200px;
  background: #e62a52;
  border-radius: 8px;
}
.square-animation {
  animation: wipe-enter 2s 1
}
@keyframes wipe-enter {
  0% {
    transform: scale(0, .025)
  }
  50% {
    transform: scale(1, .025)
  }
}

.wrap {
  display: flex;
}
.stage {
  position: relative;
  height: 250px;
  margin-top: 110vh;
}
.stage, .stage-footer {
  display: flex;
  align-items: center;
  justify-content: center;
}
.stage-footer {
  margin: 10px
}